"use client"

import { useEffect, useState } from "react"
import { useRouter, useParams } from "next/navigation"
import Link from "next/link"
import { authService } from "@/lib/auth-service"
import { requestsService, type RequestData } from "@/lib/requests-service"
import Image from "next/image"

export default function RequestDetailPage() {
  const router = useRouter()
  const params = useParams()
  const requestId = params.id as string
  const [request, setRequest] = useState<RequestData | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")
  const [changingStatus, setChangingStatus] = useState(false)

  const user = authService.getCurrentUser()

  useEffect(() => {
    const token = authService.getAccessToken()
    if (!token) {
      router.push("/login")
      return
    }
  }, [router])

  useEffect(() => {
    const fetchRequest = async () => {
      try {
        setLoading(true)
        const data = await requestsService.getRequest(Number.parseInt(requestId))
        setRequest(data)
      } catch (err) {
        console.error("[v0] Error fetching request:", err)
        setError(err instanceof Error ? err.message : "Failed to load request")
      } finally {
        setLoading(false)
      }
    }

    fetchRequest()
  }, [requestId])

  const handleStatusChange = async (newStatus: string) => {
    if (!request) return

    try {
      setChangingStatus(true)
      const updated = await requestsService.changeRequestStatus(request.id, newStatus)
      setRequest(updated)
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to update status")
    } finally {
      setChangingStatus(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <p className="text-gray-600">Loading request...</p>
      </div>
    )
  }

  if (!request) {
    return (
      <div className="min-h-screen bg-white">
        <header className="border-b border-gray-200">
          <div className="max-w-4xl mx-auto px-4 py-4">
            <Link href="/requests" className="text-gray-600 hover:text-gray-900">
              ← Back to Requests
            </Link>
          </div>
        </header>
        <main className="max-w-4xl mx-auto px-4 py-12 text-center">
          <p className="text-gray-600">{error || "Request not found"}</p>
        </main>
      </div>
    )
  }

  const canChangeStatus = user?.is_poc || user?.is_admin
  const nextStatus =
    request.status === "Submitted"
      ? ["Processing", "Cannot be Processed"]
      : request.status === "Processing"
        ? ["Issued", "Cannot be Processed"]
        : []

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="bg-white border-b border-gray-200 sticky top-0 z-40">
        <div className="max-w-4xl mx-auto px-4 py-4 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Image
              src="/images/image.png"
              alt="Smart India Hackathon"
              width={120}
              height={40}
              className="h-10 w-auto"
            />
            <Image src="/images/gitam-logo-20-282-29.png" alt="GITAM" width={60} height={40} className="h-10 w-auto" />
          </div>
          <Link href="/requests" className="text-gray-600 hover:text-gray-900 font-medium">
            ← Back to Requests
          </Link>
        </div>
      </header>

      <main className="max-w-4xl mx-auto px-4 py-8">
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
            <p className="text-red-800">{error}</p>
          </div>
        )}

        <div className="bg-white border border-gray-200 rounded-lg p-8">
          <div className="flex items-start justify-between mb-6">
            <div>
              <div className="flex items-center gap-3 mb-4">
                <span
                  className="px-4 py-2 text-white font-semibold rounded-full"
                  style={{
                    backgroundColor:
                      request.category === "BOM"
                        ? "#002449"
                        : request.category === "ADDITIONAL"
                          ? "#007367"
                          : "#f75700",
                  }}
                >
                  {request.category}
                </span>
                <span
                  className="px-4 py-2 text-white font-semibold rounded-full"
                  style={{
                    backgroundColor:
                      request.status === "Submitted"
                        ? "#f75700"
                        : request.status === "Processing"
                          ? "#002449"
                          : request.status === "Issued"
                            ? "#078e31"
                            : "#999",
                  }}
                >
                  {request.status}
                </span>
              </div>
              <h1 className="text-3xl font-bold text-gray-900">{request.notes || "Request #" + request.id}</h1>
            </div>
            <p className="text-sm text-gray-500">ID: {request.id}</p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8 pb-8 border-b border-gray-200">
            <div>
              <p className="text-xs font-medium text-gray-500 uppercase mb-1">Created</p>
              <p className="font-semibold text-gray-900">{new Date(request.created_at).toLocaleDateString()}</p>
            </div>
            <div>
              <p className="text-xs font-medium text-gray-500 uppercase mb-1">Last Updated</p>
              <p className="font-semibold text-gray-900">{new Date(request.updated_at).toLocaleDateString()}</p>
            </div>
            <div>
              <p className="text-xs font-medium text-gray-500 uppercase mb-1">Team ID</p>
              <p className="font-semibold text-gray-900 font-mono text-sm">{request.team_id}</p>
            </div>
          </div>

          {/* BOM Items */}
          {request.bom_items && request.bom_items.length > 0 && (
            <div className="mb-8">
              <h2 className="text-lg font-bold mb-4" style={{ color: "#f75700" }}>
                BOM Items
              </h2>
              <div className="border border-gray-200 rounded-lg overflow-hidden">
                <table className="w-full">
                  <thead className="bg-gray-50 border-b border-gray-200">
                    <tr>
                      <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Item Name</th>
                      <th className="px-4 py-3 text-right text-sm font-semibold text-gray-900">Quantity</th>
                    </tr>
                  </thead>
                  <tbody>
                    {request.bom_items.map((item, idx) => (
                      <tr key={idx} className="border-b border-gray-200 hover:bg-gray-50">
                        <td className="px-4 py-3 text-gray-900">{item.item_name}</td>
                        <td className="px-4 py-3 text-right font-semibold text-gray-900">{item.quantity}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}

          {/* Additional Items */}
          {request.additional_items && request.additional_items.length > 0 && (
            <div className="mb-8">
              <h2 className="text-lg font-bold mb-4" style={{ color: "#f75700" }}>
                Additional Items
              </h2>
              <div className="border border-gray-200 rounded-lg overflow-hidden">
                <table className="w-full">
                  <thead className="bg-gray-50 border-b border-gray-200">
                    <tr>
                      <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Item Name</th>
                      <th className="px-4 py-3 text-right text-sm font-semibold text-gray-900">Quantity</th>
                    </tr>
                  </thead>
                  <tbody>
                    {request.additional_items.map((item, idx) => (
                      <tr key={idx} className="border-b border-gray-200 hover:bg-gray-50">
                        <td className="px-4 py-3 text-gray-900">{item.item_name}</td>
                        <td className="px-4 py-3 text-right font-semibold text-gray-900">{item.quantity}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}

          {/* Fabrication Details */}
          {request.fabrication && (
            <div className="mb-8">
              <h2 className="text-lg font-bold mb-4" style={{ color: "#f75700" }}>
                Fabrication Details
              </h2>
              <div className="bg-gray-50 border border-gray-200 rounded-lg p-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <p className="text-xs font-medium text-gray-500 uppercase mb-1">Type</p>
                    <p className="font-semibold text-gray-900">{request.fabrication.fab_type}</p>
                  </div>
                  <div>
                    <p className="text-xs font-medium text-gray-500 uppercase mb-1">Name</p>
                    <p className="font-semibold text-gray-900">{request.fabrication.name}</p>
                  </div>
                </div>
                {request.fabrication.file && (
                  <div className="mt-4 pt-4 border-t border-gray-200">
                    <p className="text-xs font-medium text-gray-500 uppercase mb-2">File</p>
                    <a
                      href={request.fabrication.file}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="text-blue-600 hover:text-blue-800 font-medium"
                    >
                      Download File →
                    </a>
                  </div>
                )}
              </div>
            </div>
          )}

          {/* Status Change Actions */}
          {canChangeStatus && nextStatus.length > 0 && (
            <div className="pt-8 border-t border-gray-200">
              <h3 className="text-lg font-bold mb-4" style={{ color: "#f75700" }}>
                Update Status
              </h3>
              <div className="flex flex-wrap gap-3">
                {nextStatus.map((status) => (
                  <button
                    key={status}
                    onClick={() => handleStatusChange(status)}
                    disabled={changingStatus}
                    className="px-6 py-2 text-white font-medium rounded-lg transition-opacity"
                    style={{
                      backgroundColor: status === "Cannot be Processed" ? "#f75700" : "#078e31",
                    }}
                    onMouseEnter={(e) => !changingStatus && (e.currentTarget.style.opacity = "0.9")}
                    onMouseLeave={(e) => !changingStatus && (e.currentTarget.style.opacity = "1")}
                  >
                    {changingStatus ? "Updating..." : `Mark as ${status}`}
                  </button>
                ))}
              </div>
            </div>
          )}
        </div>
      </main>
    </div>
  )
}
